import React from "react";
import './Navigation.css'

export const Navigation: React.FunctionComponent<PropsType> = props => {
  return <nav className={'Navigation'}>
    <ul className={'Navigation-List'}>
      <li className={'Navigation-List_decision ListItem'}> </li>
      <li className={'Navigation-List_about ListItem'}> </li>
      <li className={'Navigation-List_blog ListItem'}></li>
      <li className={'Navigation-List_contacts ListItem'}></li>
    </ul>
  </nav>
}

type PropsType = {}